<template>
  <section class="contact-us container">
     <TheTitle :theme="theme" :title="title" />
     <ContactNav 
      @getBtnIndex="getBtnIndex" 
      :currentList="currentList"
     />
     <ContactLocations
      :currentList="currentList"
     />
  </section>
</template>

<script>
import TheTitle from '../components/The-title'
import ContactNav from '../components/CONTACTS/Contact-nav'
import ContactLocations from '../components/CONTACTS/Contact-locations'

export default {
  name: 'Contact',
  components: {
    TheTitle,
    ContactNav,
    ContactLocations
  },
  data() {
    return {
      theme: 'Main menu / Contacts',
      title: 'Contact Us',
      currentList: 1
    }
  },
  methods: {
    getBtnIndex(index) {
      this.currentList = index
    }
  },
}
</script>

<style lang="scss">
@mixin media-size($size){
  @media(max-width: $size){
    @content
  }
}

.contact-us{
  min-height: 100vh;
  padding: 220px 0 70px;
  // Media
  @include media-size(1230px) {
    padding: 170px 0 30px;
  }
  @include media-size(790px) {
    padding: 150px 0 30px;
  }
}
</style>