<template>
  <nav class="contact-nav">
    <div 
      class="button-block"
      v-for="(contry, index) in countries"
      @click="getIndex(index)"
      :key="contry.name"
    >
      <button :class="{ active: currentList === index }" >
        <img :src="contry.image" alt="">
        <span>{{contry.text}}</span>
      </button>
    </div>
  </nav>
</template>

<script>
export default {
  name: 'Contact-nav',
  props: {
    currentList: {
      type: Number,
      default: () => 1
    }
  },
  data() {
    return {
      countries: [
        {
          image: require('@/assets/images/contact/uzb-flag.png'),
          text: 'Uzbekistan'
        },
        {
          image: require('@/assets/images/contact/uk-flag.png'),
          text: 'United Kingdom'
        },
        {
          image: require('@/assets/images/contact/kaz-flag.png'),
          text: 'Kazakhstan'
        }, 
      ]
    }
  },  
  methods: {
    getIndex(index) {
      this.$emit('getBtnIndex', index)
    }
  },
}
</script>

<style lang="scss">
@mixin media-size($size){
  @media(max-width: $size){
    @content
  }
}

.contact-nav{
  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin: 30px 0 50px;
  // Media
  @include media-size(1020px) {
    flex-wrap: wrap;
  }
  
  .button-block{
    width: 350px;
    height: 80px;
    margin: 20px;
    // Media
    @include media-size(1230px) {
      width: 290px;
    }
    @include media-size(1020px) {
      width: 250px;
      height: 70px;
    }
    
    &:nth-child(2) {
      // Media
      @include media-size(1020px) {
        order: -1;
      }
    }
    
    &:hover button{
      transform: translateY(-5px);
      box-shadow: 0 40px 60px rgba(0, 0, 0, 0.25);
    }
    
    button{
      width: 100%;
      height: 100%;
      border-radius: 10px;
      background: #ffffff;
      border: none;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      transition: 0.5s;
      
      &.active{
        background: #2268c8;
        box-shadow: 0 30px 50px rgba(0, 0, 0, 0.25);
        color: #ffffff;
      }
      
      img{
        width: 100%;
        max-width: 62px;
        // Media
        @include media-size(1230px) {
          max-width: 50px;
        }
      }
      
      span{
        font-size: 25px;
        font-weight: 600;
        font-family: 'Montserrat';
        margin-left: 22px;
        // Media
        @include media-size(1230px) {
          font-size: 18px;
        }
      }
    }
  }
}
</style>